.betting-img-cust {
  width: 100%;
  pointer-events: none;
  border-radius: 5px;
}
.dollar-sign-cust {
  height: 3em;
  width: 3em;
  pointer-events: none;
}
.btn {
  width: max-content;
}
.fetching-txt-cust {
  color: gray;
}
.spinner-cust {
  display: inline-block;
}

@media (prefers-reduced-motion: no-preference) {
  .dollar-sign-cust {
    animation: Dollar-sign-spin infinite 2s linear;
    animation-delay: 0ms;
  }
}

@keyframes Dollar-sign-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}
