/* bet listing card */
p {
  margin: 0px;
  display: inline-block;
  font-family: "Open Sans", sans-serif;
}
.bet-list-card-cust {
  margin: 1em 0;
  border-radius: 9px;
  width: 100%;
}
.team-name-cust {
  font-size: 1.2rem;
  color: #424242;
  font-weight: bold;
}
.timing-cust,
.venue-cust {
  vertical-align: middle;
  font-size: 1rem;
  color: #979797;
}
.team-logo {
  height: 2em;
  width: 2em;
}
.match-type-cust {
  color: #bcbcbc;
  font-size: 0.7rem;
}
